$text-color:white;
$second-color: #464866;		
$first-hover:#B30600;
$second-hover:#2079AC;
$third-hover: #FF0700;

.projects {
    ul:nth-child(3) {
        display: grid;
        grid-template-columns: repeat(3, 1fr); 
        column-gap: 60px;
        margin: auto;
        padding: 30px 0;
        li {
            border: initial;
            border-radius: 20px;
            border-bottom-style: solid;
            border-bottom-color: $third-hover;
            text-align: center;
            background-color: $second-color;
            color: $text-color;
            h2 {
                margin-bottom: 15px;
                padding: 5px 0;
                border-top-left-radius: 20px;
                border-top-right-radius: 20px;
                font-weight: bold;
                font-size: 1.4rem;
                background-color: $first-hover;
            }

            h3 {
                margin: 0 20px 5px;
                font-weight: bold;
                font-size: 1.25rem;
            }

            p {
                padding: 0 20px 10px;
                text-align: left;
            }

            hr {
                margin: auto auto 5px; 
                width: 80%;
                border: 2px solid $third-hover;
                border-radius: 100%;
            }
    
            ul:nth-child(2) {
                margin: auto auto 10px;
                max-width: 200px;
                height: 200px;
                &:hover li {
                    transform: rotateY(180deg);
                }

                li {
                    position: relative;
                    width: 100%;
                    height: 100%;
                    border: none;
                    transition: transform 0.8s;
                    transform-style: preserve-3d;
                    text-align: center;
                    .projects-flip-box1, 
                    .projects-flip-box2,
                    .projects-flip-box3 {
                        position: absolute;
                        z-index: 1;
                        width: 100%;
                        height: 100%;
                        -webkit-backface-visibility: hidden;
                        backface-visibility: hidden;
                        cursor: pointer;
                        overflow: hidden;
                        border-radius: 10px;
                        img {
                            position: relative;
                            top: 15%;
                            max-width: 70%;
                        }
                    }
                    
                    .projects-flip-box1 {
                        background-color: #d6c6b4;
                    }
                    
                    .projects-flip-box2 {
                        background-color:#116466;
                    }
                    
                    .projects-flip-box3 {
                        background-color:$text-color;
                    }
                    
                    .projects-flip-box__triangle {
                        position:absolute;
                        left: 160px;
                        z-index: 1;
                        border-top: 100px solid $first-hover;
                        border-left: 100px solid transparent;
                    }    
                        
                    .projects-flip-box__back {
                        position: absolute;
                        z-index: 2; //pointer works properly on the back side
                        padding-top: 14px;
                        width: 100%;
                        height: 100%;
                        border-radius: 10px;
                        -webkit-backface-visibility: hidden;
                        backface-visibility: hidden;
                        transform: rotateY(180deg);
                        font-size: 1.25rem;
                        font-weight: bold;
                        background-color: $second-hover;
                        color:$text-color;
                        a {
                            display: block;
                            height: 0;
                        }

                        button:nth-child(1) {
                            display: inline-block;
                            padding: 3px 6px;
                            border: 2px solid $second-color;
                            border-radius: 10px;
                            cursor: pointer;
                            font-weight: bold;
                            font-family: Georgia, 'Times New Roman', Times, serif;
                            font-size: 1.25rem;
                            background-color: $first-hover;
                            color: $text-color;
                            &:hover {
                                filter: drop-shadow(1px 1px 5px $first-hover);
                                background-color: lightcoral;
                            }
                        }
                
                        p:nth-child(2) {
                            margin: 35px 0 -10px;
                            text-align: center;
                        }
                    
                        p:nth-child(3) {
                            text-align: center;
                        }
                    
                        a:nth-child(4) {
                            display: inline;
                            position: relative;
                            bottom: 4px;
                            padding: 3px 6px;
                            border: 1px solid $second-color;
                            border-radius: 10px;
                            font-size: 1.875rem;
                            background-color: $first-hover;
                            color: $text-color;
                            &:hover {
                                filter: drop-shadow(1px 1px 5px $first-hover);
                                background-color: lightcoral;
                            }
                        }
                    } 
                }
            }
        }

        //animation
        .pulse img {
            animation-name: pulse;
            animation-duration: 3s;
            animation-iteration-count: infinite;
        }
        
        @keyframes pulse {
            0% {transform: scale(1);}
            50% {transform: scale(1.3);}
            100% {transform: scale(1);}
        }
    }
}

@media only screen and (max-width: 1535px) {

    .projects ul:nth-child(3) {
        grid-template-columns: repeat(2, 1fr); 
        column-gap: 48px;
        row-gap: 30px; 
    }
}

@media only screen and (max-width: 1200px) {

    .projects ul:nth-child(3) {
        padding: 20px 0 30px;
        li {
            h2 {
                font-size: 1.3rem;
            }

            ul:nth-child(2) li {
                .projects-flip-box__back {
                    padding-top: 17px;
                    font-size: 1.15rem;
                    button:nth-child(1) {
                        font-size: 1.15rem;
                    }
                
                    a:nth-child(4) {
                        font-size: 1.65em;      
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 1040px) {

    .projects ul:nth-child(3) {
        grid-template-columns: repeat(1, 1fr); 
        width: 80%;
        hr {
            width: 85%;
        }
    }
}

@media only screen and (max-width: 900px) {

    .projects ul:nth-child(3) {        
         width: 100%;
    }
}

@media only screen and (max-width: 700px) {

    .projects ul:nth-child(3) {
        row-gap: 25px;
        padding: 20px 0;
        width: 80%;
        li {
            h2 {
                font-size: 1.15rem;
            }

            h3 {
                font-size: 1.1rem;
            }

            ul:nth-child(2) li {
                .projects-flip-box__back {
                    padding-top: 25px;
                    font-size: 1.05rem;
                    button:nth-child(1) {
                        font-size: 1.05rem;
                    }
            
                    button:nth-child(1),
                    a:nth-child(4) {
                        &:hover {
                            filter: none;
                            background-color: $first-hover;
                        }

                        &:active {
                            color:lightskyblue;
                        }  
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 500px) {

    .projects ul:nth-child(3) {
        width: 100%;
        li {
            p {
                padding: 0 10px 10px;
            }

            ul:nth-child(2)  {
                max-width: 150px;
                height: 150px; 
                li {
                    .projects-flip-box__triangle {
                        left: 115px;
                    }
                    
                    .projects-flip-box__back {
                        padding-top: 5px;
                        font-size: 0.938rem;
                        button:nth-child(1) {
                            font-size: 0.938rem;
                        }

                        p:nth-child(2) {
                            margin: 30px 0 -10px;
                        }
                
                        a:nth-child(4) {
                            bottom: 5%;
                            font-size: 1.5rem;      
                        }
                    }
                }
            }
        }
    }
}